* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

body {
  height: 100vh;
  background-color: #000;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  overflow: hidden;
}

.image-container {
  width: 200px;
  height: 200px;
  transform-style: preserve-3d;
  transition: all 1s ease-in-out;
  position: relative;
  transform: perspective(1000px)rotateY(0);
}

.image-container span {
  width: 100%;
  height: 200px;
  transform: rotateY(calc(var(--i) * 45deg)) translateZ(400px);
  position: absolute;
  top: 0;
  left: 0;
}

.image-container span img {
  width: 100%;
  height: 200px;
  position: absolute;
  top: 0;
  left: 0;
}

.buttons {
  width: 80%;
  position: relative;
}

.btn {
  color: #fff;
  letter-spacing: 2px;
  text-transform: uppercase;
  cursor: pointer;
  background-color: green;
  border: none;
  border-radius: 5px;
  padding: 10px 30px;
  font-size: 15px;
  font-weight: 600;
  position: absolute;
  bottom: -80px;
}

.btn:hover {
  filter: brightness(1.5);
}

.btn:active {
  transform: scale(.95);
}

.prev {
  left: 20%;
}

.next {
  right: 20%;
}

